<style lang="stylus">
@import 'config'
.cp-about
  color #333
  h2
    font-size 30px
    text-align center
  h3
    font-size 16px
    text-align center
  .banner
    background url(../assets/web/banner-about.jpg) center
    background-size auto 100%
    width 100%
    height 550px
    color #fff
    font-size 18px
    display flex
    justify-content center
    align-items center
    flex-direction column
    .p1
      font-size 40px
    .p2
      border-bottom 1px solid #fff
      margin 20px 0
  #company-profile
    padding-top 60px
    h4
      font-size 16px
      text-align center
      padding-top 20px
    p
      padding 20px 60px 0
      text-indent 2em
      font-size 14px
    .img
      display block
      margin 40px auto 0
  #application-area
    padding 60px 0 100px
    ul
      margin-top 40px
      text-align center
      li
        display inline-block
        width 240px
        margin 0 80px
        h4
          font-size 16px
          margin 20px 0
        p
          font-size 14px
  #contact-us
    background #f5f7f9
    padding 40px 0 30px
    .info1
      margin-top 40px
      height 390px
      background #fff
      background url(../assets/web/map.png) right center no-repeat #fff
      .left
        font-size 18px
        width 440px
        padding 50px 0 0 110px
        h4
          text-align center
          padding-bottom 10px
          margin-bottom 56px
          background url(../assets/web/bd1.png) bottom center no-repeat
        p
          line-height 40px
    .info2
      background-color #fff
      margin-top 15px
      overflow hidden
      padding-left 110px
      padding-bottom 35px
      .item
        float left
        width 360px
        padding-top 35px
        h4
          font-size 18px
          padding-bottom 10px
        p
          font-size 14px
.is-mobile
  .cp-about
    h2
      font-size r(30)
    h3
      font-size r(20)
    .banner
      height r(1111)
      background-image url(../assets/mobile/banner-about.jpg)
      .p1
        margin-top r(-500)
        font-size r(40)
      .p2
        font-size r(20)
      .p3
        font-size r(20)
    #company-profile
      padding-top r(30)
      h2
        font-size r(30)
      h3
        font-size r(20)
      p
        padding r(20) r(60) 0
        line-height 1.8
      .img
        margin r(40) auto 0
        width 90%
    #application-area
      padding r(60) 0
      ul
        li
          display block
          margin 0 auto r(40)
          img
            width r(240)
          p
            font-size r(20)
    #contact-us
      padding r(40) 0
      .info1
        margin r(40)
        height r(760)
        background url(../assets/mobile/map.jpg) right bottom no-repeat #fff
        background-size 100% auto
        .left
          width 100%
          padding 0
          h4
            padding r(20) 0
            margin-bottom r(35)
          p
            line-height 2
            font-size r(28)
            padding-left r(80)
      .info2
        margin 0
        padding 0
        .item
          width 50%
          padding r(30) 0 r(30) r(30)
          h4
            font-size r(28)
          p
            font-size r(22)
</style>



